@import url(https://fonts.googleapis.com/css?family=Open+Sans|Quicksand);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body{margin:0;height:100%;background-color:#1F2020}.app-container{display:flex;flex-direction:column;width:100%;height:100vh}.app-container .app{flex:1 0 auto;margin:0 auto;width:100%}@media (min-width: 960px){.app-container .app{width:800px}}

.app-footer{align-items:center;display:flex;flex-direction:column;flex-shrink:0;justify-content:center;margin-top:auto;padding:16px;background-color:#000000}.app-footer p{color:#FFFFFF}.app-footer a{color:#FF2A2B}

.app-header_top-bar{height:25px;background-color:#FF2A2B}.app-header_bottom-bar{align-items:center;display:flex;flex-direction:row;justify-content:space-around;width:100%;height:80px;background-color:#000000}.app-header_bottom-bar h1{display:inline-block;margin-right:1.5rem;margin-left:1.5rem;font-family:Quicksand,sans-serif;font-size:1.5em;font-stretch:condensed;font-weight:400;letter-spacing:0.1em;line-height:1.1;text-shadow:1px 1px 10px rgba(255,255,255,0.25),-1px -1px 10px rgba(255,255,255,0.25);text-transform:uppercase;color:#FFFFFF;transition:text-shadow 0.8s cubic-bezier(0.5, 0.18, 0.115, 0.87)}

.landing-container{display:grid;grid-gap:32px;grid-template-columns:repeat(1, 1fr);grid-template-rows:repeat(2, 1fr);justify-items:center;margin:48px 8px}@media (min-width: 960px){.landing-container{grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(1, 1fr)}}.link-button{align-items:center;display:flex;flex-direction:column;justify-content:center;border:1px solid;border-radius:0;width:100%;font-family:Quicksand,sans-serif;font-stretch:condensed;font-weight:400;letter-spacing:1px;line-height:1.1;text-decoration:none;text-shadow:1px 1px 10px rgba(255,255,255,0.25),-1px -1px 10px rgba(255,255,255,0.25);text-transform:uppercase;background:#1F2020;color:#FFFFFF;cursor:pointer}.link-button:hover{background-color:#2c2d2d}.link-button:active{border-color:#f2f2f2;background-color:#121313}.link-button:focus{outline:1px dotted #121313;outline-offset:2px}

.killer-container h2,.killer-container p{text-shadow:1px 1px 10px rgba(255,255,255,0.25),-1px -1px 10px rgba(255,255,255,0.25);color:#FFFFFF}.killer-container .killer-toolbar{align-items:center;display:flex;flex-direction:column;justify-content:space-between;margin:0 8px 32px;margin-bottom:32px}.killer-container .killer-toolbar>*:first-child{margin-bottom:16px}@media (min-width: 960px){.killer-container .killer-toolbar>*:first-child{margin-bottom:0}}@media (min-width: 960px){.killer-container .killer-toolbar{flex-direction:row;margin:0 0 32px}}

.killer{display:grid;grid-gap:16px;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(4, 1fr);margin-bottom:32px}.killer img{max-width:100%;max-height:140px}.killer>*{align-items:center;display:flex;flex-direction:column;grid-column:span 1;justify-self:center;border-bottom:2px solid #FF2A2B;padding-bottom:16px;max-width:160px}.killer>*:first-child{grid-column:span 2}@media (min-width: 960px){.killer{grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(2, 1fr)}}

.diamond-icon_name{min-height:36px;text-align:center}.diamond-icon_icon{margin-top:20px;border:8px solid #000000;width:58%;transform:rotateY(0deg) rotate(45.5deg)}.diamond-icon_icon img{transform:rotateY(0deg) rotate(-45.5deg)}.diamond-icon_icon::after{display:block;content:''}

.square-icon{justify-self:center}.square-icon_name{min-height:36px;text-align:center}.square-icon_icon{border:10px solid #000000;width:75%}.square-icon_icon::after{display:block;content:''}

@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@-webkit-keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@-webkit-keyframes expandingLine{from,10%{stroke-dashoffset:0}50%,to{stroke-dashoffset:-112px}from,10%,to{stroke-dasharray:94px 19px}50%,60%{stroke-dasharray:.4px 112px}}@keyframes expandingLine{from,10%{stroke-dashoffset:0}50%,to{stroke-dashoffset:-112px}from,10%,to{stroke-dasharray:94px 19px}50%,60%{stroke-dasharray:.4px 112px}}@-webkit-keyframes colorMorphing{0%,35%,100%{stroke:#31313D}50%,85%{stroke:#919194}}@keyframes colorMorphing{0%,35%,100%{stroke:#31313D}50%,85%{stroke:#919194}}.loading-spinner{position:relative;margin:16px 0;width:40px;height:40px;font-size:40px}.loading-spinner-container{align-items:center;display:flex;flex-direction:column;justify-content:center;width:100%}.loading-spinner-front{position:absolute;top:0;overflow:visible;width:40px;height:40px;font-size:40px;fill:transparent;stroke-linecap:round;stroke-width:4px;-webkit-animation:2s rotate infinite linear;animation:2s rotate infinite linear}.loading-spinner-front circle{-webkit-animation:2s expandingLine infinite ease-in-out, 4s colorMorphing infinite linear;animation:2s expandingLine infinite ease-in-out, 4s colorMorphing infinite linear;stroke-dasharray:94px 19px}.loading-spinner-rear{position:absolute;top:0;overflow:visible;width:40px;height:40px;font-size:40px;fill:transparent;stroke-linecap:round;stroke-width:4px}.loading-spinner-rear circle{-webkit-animation:15s colorMorphing infinite linear;animation:15s colorMorphing infinite linear;stroke-opacity:0.15}

.survivor-container h2,.survivor-container p{text-shadow:1px 1px 10px rgba(255,255,255,0.25),-1px -1px 10px rgba(255,255,255,0.25);color:#FFFFFF}.survivor-container .survivor-toolbar{align-items:center;display:flex;flex-direction:column;justify-content:space-between;margin:0 8px 32px;margin-bottom:32px}.survivor-container .survivor-toolbar>*:first-child{margin-bottom:16px}@media (min-width: 960px){.survivor-container .survivor-toolbar>*:first-child{margin-bottom:0}}@media (min-width: 960px){.survivor-container .survivor-toolbar{flex-direction:row;margin:0 0 32px}}

.survivor{display:grid;grid-gap:16px;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(4, 1fr);margin-bottom:32px}.survivor img{max-width:100%;max-height:140px}.survivor>*{align-items:center;display:flex;flex-direction:column;grid-column:span 1;justify-self:center;border-bottom:2px solid #FF2A2B;padding-bottom:16px;max-width:160px}.survivor_perks{grid-row-start:2}.survivor .firecracker-buffer{display:none;grid-column:span 2}@media (min-width: 960px){.survivor .firecracker-buffer{display:block}}@media (min-width: 960px){.survivor{grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(2, 1fr)}}

